import React, { useEffect } from "react";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import SubCategoriesCardLeft from "../../../templates/subcategories/sub-categories-card-left/SubCategoriesCardLeft";

import SubCategoriesCardRight from "../../../templates/subcategories/sub-categories-card-right/SubCategoriesCardRight";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import "./SocialMediaBranding.css";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"
import pic3 from "./pic3.png"
import pic4 from "./pic4.png"
import pic5 from "./pic5.png"
import pic6 from "./pic6.png"
import pic7 from "./pic7.png"

import socialMediaBranding from "./SocialMediaBrandManagement.jpg";

import { useLocation } from "react-router-dom";

export default function SocialMediaBranding() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="social-media-branding-main-container">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="Social Media Brand Management"
        bannerParagraph="Helping your business reach new heights through effective branding and engagement strategies."
        bannerButton1="Let’s chat"
        bannerButton2="Our work"
        bannerBackgroundImage={socialMediaBranding}
      />

      <SubCategoriesCardLeft paragrapgh="Interac8 provides businesses with a powerful tool to reach wide audiences through personalized marketing campaigns and engaging activities that create memorable experiences between brands and customers. Brands can leverage social networks by creating unique content that resonates among their followers while increasing awareness and support across all other platforms they utilize (e-commerce stores websites etc)." 
      picture={pic1}/>

      <SubCategoriesCardRight paragraph="Through strategic campaigns, companies are able to gain greater visibility online which leads not only to more sales but -- ultimately --- increased brand loyalty between themselves and consumers over time thus becoming extremely beneficial in growing business successfully." picture={pic2}/>

      <SubCategoriesCardLeft paragrapgh="We start by clearly stating your brand's values, mission, target market, and differentiators. Your social media branding initiatives will be built on this." picture={pic3} />

      <SubCategoriesCardLeft paragrapgh="We create a content strategy that reflects your company's ideals and appeals to your target market. Informational, interesting, and shareable material is what you should aim for. Mix text with photos, movies, and other multimedia types to keep your audience interested." picture={pic4}/>

      <SubCategoriesCardLeft paragrapgh="Actively interac8 with your audience by replying to comments, messages, and mentions on social media, which is a two-way communication channel. Encourage discussion, pose inquiries, and look for feedback." picture={pic5}/>

      <SubCategoriesCardRight
        heading="Work together with influencers"
        paragraph="We collaborate with influencers in your sector to help expand the reputation and reach of your brand. Find influential people who share your values for your business and engage them to spread the word about your goods or services to their audiences."
        picture={pic6}
      />

      <SubCategoriesCardLeft
        heading="Monitor and evaluate your performance"
        paragrapgh="To determine the success of your branding initiatives, we keep a close eye on your social media data. Keep track of analytics like conversions, reach, and follower growth. Refine your plan and make data-driven decisions using your newfound insights."
        picture={pic7}
      />

      <p className="social-media-branding-main-container-paragraph" >
        Social media platforms and trends are constantly evolving, so stay
        informed about the latest updates and features. Experiment with new
        formats, hashtags, or features to keep your brand fresh and relevant.
      </p>

      <br/>
      <br/>
      <br/>
      <br/>

      <Pricing/>

      <br/>
      <br/>
      <br/>

      <Skills/>

      <br/>
      <br/>
      <br/>

      <Ourwork/>

      <Footer/>

    </div>
  );
}
